@import 'bem/btn';

body {
	margin: 0;
	padding: 0;
	font-family: 'Raleway', sans-serif;
}


*,
*:before,
*:after {
	box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 0;
	color: #000;
}

p {
	margin: 0;
	color: #000;
	font-size: 16px;
}


ul,
li {
	margin: 0;
	padding: 0;
	list-style-type: none;
}

a {
	text-decoration: none;
	transition: all .3s ease-in-out;

	&:hover {
		text-decoration: none;
	}
}

.clearfix {

	&:before,
	&:after {
		display: table;
		content: ' '
	}

	&:after {
		clear: both;
	}
}